import {Home, Blog, AboutMe, Contact,Login, Name, Phone} from "../pages/index"

const publicRoutes = [
    {
        path: "/", 
        element : <Home/>
    },
    {
        path: "blog", 
        element : <Blog/>
    },
    {
        path: "aboutme", 
        element : <AboutMe/>, 
        route : [ 
            {
                path: "name",
                element: <Name/>
            },
            {
                path: "phone",
                element: <Phone/>
            }
        ]
    },
    {
        path: "login", 
        element : <Login/>
    },
    {
        path: "contact", 
        element: <Contact/>
    }

];
export {publicRoutes}